import PropTypes from "prop-types";
import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import Nav from "react-bootstrap/Nav";
import { Link } from "react-router-dom";
// import { Link } from "react-router-dom";
// import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";
// import Tab from "react-bootstrap/Tab";
// import Nav from "react-bootstrap/Nav";
import LayoutThree from "../../layouts/LayoutThree";
// import Breadcrumb from "../../wrappers/breadcrumb/Breadcrumb";
import { useFormik } from "formik";
import * as Yup from "yup";

const Register = ({ location }) => {
  const { pathname } = location;
  const formik = useFormik({
    initialValues: {
      fname: "",
      lname: "",
      email: "",
      phone: "",
      password: "",
      confirm_password: "",
    },
    validationSchema: Yup.object({
      fname: Yup.string()
        .min(2, "Mininum 2 characters")
        .max(15, "Maximum 15 characters")
        .required("First name is a required field!"),

      lname: Yup.string()
        .min(2, "Mininum 2 characters")
        .max(15, "Maximum 15 characters")
        .required("Last name is a required field!"),

      email: Yup.string()
        .email("Invalid email format")
        .required("Email is a required field!"),

      phone: Yup.string().required("phone Number is a required field!"),

      password: Yup.string()
        .min(6, "Minimum 6 characters")
        .required("password is a required field!"),

      confirm_password: Yup.string()
        .oneOf([Yup.ref("password")], "Password's not match")
        .required("confirm_password is a required field!"),
    }),
    onSubmit: (values, { resetForm }) => {
      alert(JSON.stringify(values, null, 2));
      resetForm({ values: "" });
    },
  });
  return (
    <Fragment>
      <MetaTags>
        <title>Impetrosys</title>
        <meta name="description" content="login" />
      </MetaTags>
      <LayoutThree headerTop="visible">
        <div className="login-register-area pt-30 pb-50">
          <div className="container">
            <div className="row">
              <div className="col-lg-7 col-md-12 ml-auto mr-auto">
                <div className="login-register-wrapper">
                  <div className="login-form-container">
                  <Nav variant="pills" className="login-register-tab-list">
                      <Nav.Item>
                      <Link  to={process.env.PUBLIC_URL + "/Login"}>
                      <h4>Login</h4>
                        </Link>
                      </Nav.Item>
                      <Nav.Item>
                      <Link  to={process.env.PUBLIC_URL + "/Register"}>
                      <h4>Register</h4>
                        </Link>
                      </Nav.Item>
                    </Nav>
                    <div className="login-register-form">
                      <form onSubmit={formik.handleSubmit}>
                        <div className="container">
                          <div className="row">
                            <div className="col-lg-6">
                              <input
                                type="text"
                                name="fname"
                                placeholder="First Name"
                                value={formik.values.fname}
                                onChange={formik.handleChange}
                              />
                              {formik.errors.fname && formik.touched.fname && (
                                <p className="text-danger">
                                  {formik.errors.fname}
                                </p>
                              )}
                            </div>
                            <div className="col-lg-6">
                              <input
                                type="text"
                                name="lname"
                                placeholder="Last Name"
                                value={formik.values.lname}
                                onChange={formik.handleChange}
                              />
                              {formik.errors.lname && formik.touched.lname && (
                                <p className="text-danger">
                                  {formik.errors.lname}
                                </p>
                              )}
                            </div>
                            <div className="col-lg-6">
                              <input
                                name="email"
                                placeholder="Email"
                                type="email"
                                value={formik.values.email}
                                onChange={formik.handleChange}
                              />
                              {formik.errors.email && formik.touched.email && (
                                <p className="text-danger">
                                  {formik.errors.email}
                                </p>
                              )}
                            </div>
                            <div className="col-lg-6">
                              <input
                                name="phone"
                                placeholder="Mobile Number"
                                type="number"
                                value={formik.values.phone}
                                onChange={formik.handleChange}
                              />
                              {formik.errors.phone && formik.touched.phone && (
                                <p className="text-danger">
                                  {formik.errors.phone}
                                </p>
                              )}
                            </div>

                            <div className="col-lg-6">
                              <input
                                name="password"
                                placeholder="Password"
                                type="password"
                                value={formik.values.password}
                                onChange={formik.handleChange}
                              />
                              {formik.errors.password &&
                                formik.touched.password && (
                                  <p className="text-danger">
                                    {formik.errors.password}
                                  </p>
                                )}
                            </div>

                            <div className="col-lg-6">
                              <input
                                name="confirm_password"
                                placeholder=" Confifm Password"
                                type="Password"
                                value={formik.values.confirm_password}
                                onChange={formik.handleChange}
                              />
                              {formik.errors.confirm_password &&
                                formik.touched.confirm_password && (
                                  <p className="text-danger">
                                    {formik.errors.confirm_password}
                                  </p>
                                )}
                            </div>
                            <div className="col-lg-12">
                              <div className="button-box-new">
                                <button
                                  type="submit"
                                  className="button-box-new"
                                >
                                  <span>Register</span>
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutThree>
    </Fragment>
  );
};

Register.propTypes = {
  location: PropTypes.object,
};

export default Register;
